.modal {
    font-size: 12px;
    
  }
  .modal > .header {
    width: 100%;
    /* text-align: center; */
    padding: 5px;
    font-family:  'NotoSansKR-Medium';
    font-size:30px;
    padding: 5px 5px 5px 20px;
    border-left : 10px solid #fcc419;
    border-right : 10px solid white;

    /*
    background: 
      linear-gradient(
        to left, 
        #fff9db 0%,
        #ffd43b 50%,
        #fd7e14 100%
      )
      left 
      bottom
      white    
      no-repeat; 
    */
    /*background-size:100% 2px ; *//* if linear-gradient, we need to resize it */
   
  }
  .modal > .content {
    box-shadow: 2px 2px 10px grey;
    width: 100%;
    padding: 10px 5px;
    text-align: center;
    background: #f1f3f5;

    margin-top: 10px;
  }
  .modal > .actions {
    width: 100%;
    padding: 10px 5px;
    margin: auto;
    text-align: center;
  }
  .modal > .close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 0px 2px 2px 2px;
    line-height: 20px;
    right: -10px;
    top: -10px;
    font-size: 24px;
    background: #ffffff;
    border: 1px solid #cfcece;
  }

