
.article-wrapper{
    //position: absolute;
    width: (100%);
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;

    .article{
        background: #ffffff;
        -webkit-border-radius: 4px;
        border-radius: 4px;
        -webkit-box-shadow: 0 3px 10px rgba(0,0,0,0.1);
        box-shadow: 0 3px 10px rgba(0,0,0,0.1);
    
  
            .article-thumbnail{
                width: 100%;
                padding-top: 52.63%;
                position: relative;
                display: block;
            }
            .article-thumbnail img {
                background: #dee2e6;
                -o-object-fit: cover;
                object-fit: cover;
                display: block;
                border-top-left-radius: 4px;
                border-top-right-radius: 4px;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;;
            }

        
        .content-wrapper{
            padding: 10px;
            .content-header{
                position: relative;
                height: 7.0rem;
                padding-bottom: 1.5rem;
                border-bottom: 1px solid #e9ecef;

                .content-title{
                    font-size: 25px;
                }
                .content-author{
                    font-size: 10px;
                }
                .content-date{
                    font-size: 10px;
                }
                .content-view{
                    font-size: 10px;
                }
            }


            .content-content{
                margin-top: 1.5rem;
                margin-bottom: 3.5rem;
                line-height: 1.5rem;
                height: 4.5rem;
                overflow-y: hidden;
                word-break: break-all;
                color: #4c657d;
                display: -webkit-box;
                -webkit-line-clamp: 3;
                overflow: hidden;
                -o-text-overflow: ellipsis;
                text-overflow: ellipsis;
               
            }
        }
      .article-footer{
          color : red;
      }

    }

}


