
@font-face{font-family:'BlackHanSans-Regular'; src:url('../../Styles/fonts/BlackHanSans-Regular.ttf')}
@font-face{font-family:'JejuHallasan-Regular'; src:url('../../Styles/fonts/JejuHallasan-Regular.ttf')}
@font-face{font-family:'JejuMyeongjo-Regular'; src:url('../../Styles/fonts/JejuMyeongjo-Regular.ttf')}
@font-face{font-family:'NotoSansKR-Medium'; src:url('../../Styles/fonts/NotoSansKR-Medium.otf')}
@font-face{font-family:'EastSeaDokdo-Regular'; src:url('../../Styles/fonts/EastSeaDokdo-Regular.ttf')}

/* Set dropdown font-families */
.ql-font span[data-label="BlackHanSans"]::before {
    font-family: "BlackHanSans-Regular";
  }
.ql-font span[data-label="JejuHallasan"]::before {
    font-family: "JejuHallasan-Regular";
}
.ql-font span[data-label="JejuMyeongjo"]::before {
    font-family: "JejuMyeongjo-Regular";
}
.ql-font span[data-label="NotoSansKR"]::before {
    font-family: "NotoSansKR-Medium";
}
.ql-font span[data-label="EastSeaDokdo"]::before {
  font-family: "EastSeaDokdo-Regular";
}
  
  /* Set content font-families */
  .ql-font-BlackHanSans {
    font-family: "BlackHanSans-Regular";
  }
  .ql-font-NotoSansKR {
    font-family: "NotoSansKR-Medium";
  }
  .ql-font-JejuMyeongjo {
    font-family: "JejuMyeongjo-Regular";
  }
  .ql-font-JejuHallasan {
    font-family: "JejuHallasan-Regular";
  }
  .ql-font-EastSeaDokdo {
    font-family: "EastSeaDokdo-Regular";
  }
  /* We do not set Aref Ruqaa since it is the default font */
  

/* Set content for font-families */
.ql-font-arial,
.ql-font span[data-value="arial"]::before {
  font-family: Arial, sans-serif;
}
.ql-font-comic-sans,
.ql-font span[data-value="comic-sans"]::before {
  font-family: "Comic Sans MS", cursive, sans-serif;
}
.ql-font-courier-new,
.ql-font span[data-value="courier-new"]::before {
  font-family: "Courier New";
}
.ql-font-georgia,
.ql-font span[data-value="georgia"]::before {
  font-family: Georgia, serif;
}
.ql-font-helvetica,
.ql-font span[data-value="helvetica"]::before {
  font-family: Helvetica, sans-serif;
}
.ql-font-lucida,
.ql-font span[data-value="lucida"]::before {
  font-family: "Lucida Sans Unicode", "Lucida Grande", sans-serif;
}

/* Set content for sizes */
.ql-size-extra-small,
.ql-size span[data-value="extra-small"]::before {
  font-size: 13px !important;
}
.ql-size-small,
.ql-size span[data-value="small"]::before {
  font-size: 14px !important;
}
.ql-size-medium,
.ql-size span[data-value="medium"]::before {
  font-size: 18px !important;
}
.ql-size-large,
.ql-size span[data-value="large"]::before {
  font-size: 20px !important;
}

.ql-size-huge,
.ql-size span[data-value="huge"]::before {
  font-size: 30px !important;
}

.rawHTML{
  width: 100%;
  margin: 0px;
  background: rgb(29, 29, 29);
  box-sizing: border-box;
  color: rgb(204, 204, 204);
  font-size: 15px;
  outline: none;
  padding: 20px;
  line-height: 24px;
  font-family: Consolas, Menlo, Monaco;
  position: absolute;
  top: 0;
  bottom: 0;
  border: none;
  display:none;
}

.ql-editor  { display: block; }
.raw-editor {   
  width: 100%;
  
  margin: 0px;
  background: rgb(29, 29, 29);
  border: none;
  display:none;
  
  box-sizing: border-box;
  color: rgb(204, 204, 204);
  font-size: 15px;


  outline: none;
  padding: 20px;
  line-height: 24px;
  font-family: Consolas, Menlo, Monaco;
  top: 0;
  bottom: 0;



}
.showRaw .ql-editor  { display: none; }
.showRaw .raw-editor { display: block; }