.cardviewWraapper{
    display: flex;
    justify-content: center;
}

.headPostsName{
    padding :0.3em;
}
.title{
    font-size: 22px;
    margin-top: 0;
    word-break: break-all;
}
.panel{
    width: 660px;
    margin-bottom: 20px;
    background-color: #fff;
    border-radius: 4px;
    padding-bottom: 15px;
    margin: 10px;
    box-shadow: 2px 2px 10px grey;
}
.panelHeading {
    padding: 10px 15px;
    border-color: #ddd;
    border-bottom: 1px solid transparent;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
}

.panelBody{
    padding: 15px;
}

.contentText{
    
    overflow-x: auto;
    min-height: 180px;
    margin: 30px 0;
    word-break: break-all;
    word-wrap: break-word;
}
.boardButtonCompo{
    margin-left: 10px;
}