.editButton{
    background-color: #e67700; /* Green */
    border-radius: 3px;
    font-family:  'NotoSansKR-Medium';
    border: none;
    color: white;
    padding: 3px 10px 6px 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 15px;
    transition-duration: 0.4s;
}