.center{
    width: 100%;
   
}

.left{
    width: 49%;
    margin-right: 1%
}
.right{
    width: 49%;
    margin-left: 1%
}

.buttonComponent{
    margin-left: 10px;
}

.searchButtonClickContainer{
    display: inline-block;
}
.searchbar_name{
    font-size : 1.1rem;
    float: left;
    padding:5px 10px 0px 15px;
}


.searchButton{
    background-color: grey; /* Green */
    border: none;
    color: white;
    padding: 1px 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    transition-duration: 0.4s;
}
.searchButton:hover {
    
    background-color: #51cf66; /* Green */
    color: white;
  }
.searchButtonClick{
    background-color:  #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 1px 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    transition-duration: 0.4s;
}

.searchButtonClick:hover {
    background-color:  #51cf66; /* Green */
    border: none;
    color: white;
    padding: 1px 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}