@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSans-kr.css);
@font-face{font-family:'EastSeaDokdo-Regular'; src:url('./Styles/fonts/EastSeaDokdo-Regular.ttf')}
@font-face{font-family:'BlackHanSans-Regular'; src:url('./Styles/fonts/BlackHanSans-Regular.ttf')}

* { font-family: 'Spoqa Han Sans', 'Spoqa Han Sans JP', 'Sans-serif'; }
html{
  height: 100%;
  scroll-behavior: smooth;
}
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root{
  height: 100%;
}

.highcharts-container{
  height: 100% !important;
}
.componentContainer{
  position: relative;
  width: 100%;
  height: 100%;
  --margin:3vh auto 3vh;
  border-radius: 15px;
  border: 10px solid rgba(255,255,255,.15);
  box-shadow: 1px 1px 4px rgba(0,0,0,.3);
  z-index: 5;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  font:black;
  font-size: 0.7rem;
}

.componentContainer:before {
content: '';
position: absolute;
top: 0; left:0; right:0; bottom:0;
background: inherit;
filter: blur(5px); 
-webkit-filter: blur(6px); 
-moz-filter: blur(6px);
-o-filter: blur(6px);
-ms-filter: blur(6px);
filter: url(#blur);
filter:progid:DXImageTransform.Microsoft.Blur(PixelRadius='6');
background-color: rgba(255, 255, 255, 0.13);
}
.react-resizable-handle{
  z-index: 999;
}
/*일단대기 */

.carousel-item > img{
  width: 100%; 
  height: 100%;
  background-size: cover;
  object-fit: cover;
}
.carousel-inner > .item {
  height: 300px;
  height: 100%;
}

.carousel-item, .carousel-item.active {
  font-size:30px;
  align-items:center;
  height: 300px;
}

.carousel-caption h3{
  font-family:"BlackHanSans-Regular" !important;
}
.carousel-caption p{
  font-family:"BlackHanSans-Regular" !important;
}





pre {
  background: grey;
  padding : 10px;
}