.componentContainer{
  position: relative;
  width: 100%;
  height: 100%;
  --margin:3vh auto 3vh;
  border-radius: 15px;
  border: 10px solid rgba(255,255,255,.15);
  box-shadow: 1px 1px 4px rgba(0,0,0,.3);
  z-index: 5;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  font:black;
  font-size: 0.7rem;
}

.componentContainer:before {
content: '';
position: absolute;
top: 0; left:0; right:0; bottom:0;
background: inherit;
filter: blur(5px); 
-webkit-filter: blur(6px); 
-moz-filter: blur(6px);
-o-filter: blur(6px);
-ms-filter: blur(6px);
