.highcharts-loading {
    /*opacity: 1!important;*/
    left: 75px !important; /* 원래없었음  추가 */
    background:none !important;
  }
  .highcharts-loading-inner {
    display: block;
  }
  
  .highcharts-loading-inner,
  .highcharts-loading-inner:before,
  .highcharts-loading-inner:after {
    background: #dfdfdf;
    -webkit-animation: load1 1s infinite ease-in-out;
    animation: load1 1s infinite ease-in-out;
    width: 1em;
    height: 4em;

  }
  .highcharts-loading-inner {
    color: #dfdfdf;
    text-indent: -9999em;
    margin: 0 auto;
    top: 50%!important;
    position: relative;
    font-size: 11px;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
  }
  .highcharts-loading-inner:before,
  .highcharts-loading-inner:after {
    position: absolute;
    top: 0;
    content: '';
  }
  .highcharts-loading-inner:before {
    left: -1.5em;
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
  }
  .highcharts-loading-inner:after {
    left: 1.5em;
  }
  @-webkit-keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em;
    }
    40% {
      box-shadow: 0 -2em;
      height: 5em;
    }
  }
  @keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em;
    }
    40% {
      box-shadow: 0 -2em;
      height: 5em;
    }
  }
  
