.boardSelect{
    margin:3px 0px 0px 10px;
}
.saveButton{
    background: #339af0;
    height: 2.25rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    padding: 0 .75rem;
    font-weight: 500;
    border-radius: 2px;
    line-height: .875rem;
    font-size: .875rem;
    -webkit-box-shadow: 0 3px 6px rgba(0,0,0,.12), 0 3px 6px rgba(0,0,0,.1725);
    box-shadow: 0 3px 6px #0000001f, 0 3px 6px rgba(0,0,0,.1725);
    -webkit-transition: all .1s ease-in;
    -o-transition: .1s all ease-in;
    transition: all .1s ease-in;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: pointer;
}
