.myPageContainer{
    width: 500px;
    height: 70%;
    margin: 40px auto;
    border: 0.5px solid white;
    box-shadow: 2px 2px 10px grey;
    /*background: red;*/
}
.outer {
    display: table;
    width: 100%;
    height: 100%;
  }

  .inner {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
  }

  .centered {
    position: relative;
    display: inline-block;
    margin-bottom: 20px;
    width: 60%;
    padding: 1em;
    /*background: orange;*/
    color: black;
  }